@import url(https://fonts.googleapis.com/css2?family=Luxurious+Script&family=Smooch&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gloria+Hallelujah&display=swap);
.recipe-list{
 
 min-height: 100vh;
 width: 100%;
 padding: 1rem;
 transition: all ease-in 2s ;
}
.recipe-list__add-recipe-btn-container{
 text-align: center;
 padding: 1rem 2rem 3rem 2rem;
}
.recipe-list__search-bar-form {
    width: 100%;
   text-align: center;
   padding: 2rem 1rem 2rem 1rem;
    

}
.row{

    --bs-gutter-x: 0rem;
}
.col .recipe-list{
   
    border-right: 1px solid rgb(197, 193, 193);
}


input.recipe-list__search-bar{
    width: 100%;
    max-width: 12rem;
    font-size: inherit;
    padding: .5rem .5rem;
    border: 5px solid rgba(128, 128, 128,0.5);
    border-radius: .2rem;
    text-align: center;
    margin: .5rem;
    outline: none;
    }

.visually-hidden {
    clip: rect(0 0 0 0);
    -webkit-clip-path: inset(50%);
            clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: -100px;
}

@font-face {
  font-family: Gotham Rounded;
  src: url(/static/media/GothamRounded-Bold.a17abd06.otf) format('otf'),
    url(/static/media/GothamRounded-Bold.a17abd06.otf) format('otf');
  font-weight: bold;
  
  font-stlye: normal;
  
}
@font-face {
  font-family: Gotham Rounded;
  src: url(/static/media/GothamRounded-LightItalic.8a29db32.otf) format('otf');
  font-weight: 800;
  font-stlye: italic;
  
}
@font-face {
    font-family: Gotham Rounded;
    src: url(/static/media/GothamRounded-Book.e40570a3.otf) format('otf');
    font-weight: 700;
    font-stlye: normal;
   
  }

.btn{
font-size: inherit;
border: none;
outline: none;
background: none;
border-radius: .2rem;
padding:.2rem .7rem;
cursor: pointer;
margin: .2rem;
}
.btn--primary{
 color: whitesmoke;
 
 background-color: hsl(200, 100%, 50%);
}
.btn--primary:hover{
    background-color: hsl(200, 100%, 35%);
}
.btn--danger{
    text-align: center;
    color: whitesmoke;
    background-color: hsl(0, 100%, 50%);
   }
   .btn--danger:hover{
       background-color: hsl(0, 100%, 35%);
   }
.recipe{
 padding: 1rem;
 padding-bottom: 2rem;
 border-bottom: 4px solid rgb(197, 193, 193);
 /* text-shadow: -.1px .5px .5px rgba(59, 59, 59,.6); */
 color: #ffff;
}

.recipe:last-child{
    border-bottom: none;
    padding-bottom: 1rem;
}
.recipe__header{
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
    align-content: center;
    justify-items: center;
}
.recipe-buttons{
    display: block;
    margin-block: auto;
}
.recipe__title{
    margin:0;
    font-size: 1.5rem;
    color: rgb(221, 168, 33);
}
.recipe__row{
    margin-top: 1rem;

}
.recipe__label{
    margin-right: .2rem;
}
.recipe__value{
    font-weight: 500;
    
}

.recipe__value--indented{
 margin-top: .8rem;
 margin-left: 1rem;
}
.recipe__value--instructions{
  white-space: pre-wrap;  
}
.author-name{
    padding-bottom: .4rem;
}


.ingredient-grid{
    display: grid;
    grid-template-columns: repeat(2,auto);
    grid-row-gap: .5rem;
    row-gap: .5rem;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    justify-content: flex-start;
}
.author-list{
    display: block;
    margin: .2rem;
    padding: .2rem;

}

.recipe-edit{
    position:relative;
    padding: 1rem;
    padding-top:.4rem;
    
    width:100%;
    max-height: 100%;
    overflow-y: auto;
    transition: ease-in 2s;
}

.recipe-edit__remove-button-container{
    text-align: end;
}
.recipe-edit__remove-button{
    padding: 0;
    background:none;
    font-size: 1.5rem;
    outline: none;
    border: none;
    transition: cubic-bezier(1, 1, 1, 1) .5s;
}
.recipe-edit__remove-button:hover{
    -webkit-transform: scale(120%);
            transform: scale(120%);
    transition: all .4s;
}
.recipe-edit__details-grid{
   display: grid;
   grid-template-columns: auto 1fr; 
   padding: .2rem;
   grid-row-gap: .5rem;
   row-gap: .5rem;
   grid-column-gap: 1.5rem;
   -webkit-column-gap: 1.5rem;
           column-gap: 1.5rem;
}
.recipe-edit__label{
    font-weight: bold;
    color:rgb(255, 255, 255);
}
.recipe-edit__input{
    border: 1px solid black;
    border-radius: .125rem;
    font-size: inherit;
    padding: .2rem .2rem;
    outline: none;
    width: 100%;
    max-width: 25rem ;
    
}
textarea.recipe-edit__input{
    resize: none;
    min-height: 8rem;
    text-align: start;
    
    
}
.recipe-edit__ingredient-grid{
   display: grid;
   grid-template-columns: 1fr 1fr auto;
   grid-row-gap: 1rem;
   row-gap: 1rem;
   grid-column-gap: 1rem;
   -webkit-column-gap: 1rem;
           column-gap: 1rem;
   margin: 1rem;
   max-width: 30rem;
   color:#ffff;
}
.recipe-edit__add-ingredients-btn-container{
    text-align: center;
}
.recipe-edit__author-grid .recipe-edit__input,.recipe-edit__ingredient-grid .recipe-edit__input{
    text-align: center;
}

@media (max-width:1500px){
    .recipe-edit__details-grid,
    .recipe-edit__ingredient-grid,
    .recipe-edit__author-grid
    {
        grid-template-columns: 1fr;
    }
}

.recipe-edit__author-grid{
    display: grid;
    grid-template-columns: 1fr auto;
    grid-row-gap: 1rem;
    row-gap: 1rem;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    margin: 1rem;
    max-width: 15rem;
    color: #fff;
 }
.no-recipe-display{
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    height: -webkit-max-content;
    height: max-content;
    margin: 3rem 1rem;
    justify-self: center;
    align-self: center;
    text-align: center;
    color: rgb(223, 175, 19);
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    line-height: 1.5;
    font-weight: 600;
    max-width: 1400px;
}

.header-wrapper{
    width: 100%;
    
    margin: 0;
    padding:0;
    height: 17rem;
  
    line-height: .5;

background-color: #f6c716;
opacity: 0.8;
background-image: radial-gradient(circle at center center, #fafafa, #f6c716), repeating-radial-gradient(circle at center center, #fafafa, #fafafa, 4px, transparent 8px, transparent 4px);
background-blend-mode: multiply;
    
}
.text-wrapper{
    display:flex;
    flex-wrap: wrap;
    align-tracks: center;
    padding:5rem 3rem 0rem 3rem;
    justify-content: center;

}
.text-sub{
    display: flex;
    justify-content: center;
    color: #3aa5a8;
    font-family: 'Gloria Hallelujah', cursive;
    font-size: 1.3rem;
}
.text-center{
    font-family:'Smooch','Luxurious Script', cursive;
    font-size: clamp(2rem,5.5vw,5rem);
    color: #3aa5a8;
    font-weight: 400;
    padding-right: .8rem;
    
}
.text-between{
    color: rgb(255, 255, 255);
    font-size: clamp(1.4rem,3.4vw,4rem);
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
}
.dash{
  color: #3aa5a8;
  font-size: 2rem; 
  


}

.footer-wrapper{
    width: 100%;
    height: -webkit-max-content;
    height: max-content;
    background-color: darkslategray;
    padding:.8rem ;
    text-align: center;
}
.footer-wrapper p{
    margin:0;
    color: darkorange;
}
/* Document
 * ========================================================================== */
/**
 * 1. Correct the line height in all browsers.
 * 2. Prevent adjustments of font size after orientation changes in
