figure {
	font-size: 6px;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%,-50%);
	        transform: translate(-50%,-50%);
	width: 64em;
}
figcaption {
	color: #fff;
	display: flex;
	align-content: space-between;
	flex-wrap: wrap;
	height: 17em;
}
figcaption span:before, .sad-mac:before {
	content: "";
	display: block;
	width: 1em;
	height: 1em;
	-webkit-transform: translate(-1em,-1em);
	        transform: translate(-1em,-1em);
} 
figcaption span {
	display: inline-block;
	margin: 0 2em;
	width: 4em;
	height: 6em;
}
.sr-text {
	overflow: hidden;
	position: absolute;
	width: 0;
	height: 0;
}
