/* III. Responsiveness */
/* This cannot be smoothly done using viewport units; sprite pixels will look divided when font size is a floating point. */
@media screen and (min-width: 720px) {
	figure {
	    font-size: 7px;
	}
}
@media screen and (min-width: 1440px) {
	figure {
	    font-size: 8px;
	}
