.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  margin-top: -.6rem;
  width: 70vw;
  justify-content: flex-end;
  margin-right: -15rem;
  transition: .3s ease-out;  
}

.nav-item {
  display: flex;
  align-items: center;
  height: 81px;
}

.nav-links {
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem;
  top: 10px;
}

.nav-links:hover {
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #fff;
  text-shadow: 2px 2px 5px rgba(29, 22, 1, 0.5);
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: -webkit-max-content;
    height: max-content;
    position: absolute;
    top: 81px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    justify-content: flex-end;
    padding-block: 5rem;
    
  }

  .nav-menu.active {
    background: rgb(14, 109, 116);
    left: 0;
    opacity: 1;
    transition: all 0.3s ease;
    z-index: 2;
    align-self: center;
    justify-content: center;
    height: 100vh;
  }

  .nav-links {
    text-align: center;
    padding: 1.4rem;
    width: 100%;
    display: table;
    color: rgba(251, 251, 251, 0.842);
  }

  .nav-links:hover {
    background: rgb(231, 181, 15);
    background: radial-gradient(
      circle,
      rgba(231, 181, 15, 40%) 0%,
      rgba(214, 143, 10, 0.3) 93%
    );
    border-radius: 3px;
    color: #fff;
  }

  .fa-coffee {
    font-size: 2.5rem;
    display: block;
    text-align: center;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: -.6rem;
    right: 0;
    -webkit-transform: translate(-100%, 25%);
            transform: translate(-100%, 25%);
    font-size: 2rem;
    cursor: pointer;
    padding-top: 0px;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 0.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 100%;
    background: transparent;

    text-decoration: none;
    color: rgb(117, 99, 21);
    font-size: 1.4rem;
  }

  .nav-links-mobile:hover {
    background: rgba(117, 87, 5, 0.842);

    color: #fff;
    border-radius: 5px;
    transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    border: 5px solid wheat;
  }

  button {
    display: none;
  }
}

